'use client';

import { useState } from 'react';
import { useRouter } from 'next/navigation';
import { patientsAPI } from '@/lib/api';
import { Card, CardHeader, CardTitle, CardContent } from '@/components/ui/Card';
import { Input } from '@/components/ui/Input';
import { Button } from '@/components/ui/Button';

export default function NewPatientPage() {
  const router = useRouter();
  const [form, setForm] = useState({ name: '', dob: '', email: '', contact: '', disease: '', history: '', prescription: '' });
  const [files, setFiles] = useState<FileList | null>(null);
  const [error, setError] = useState('');

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setError('');
    const fd = new FormData();
    Object.entries(form).forEach(([k, v]) => v && fd.append(k, v as string));
    if (files) Array.from(files).forEach(f => fd.append('images', f));
    try {
      await patientsAPI.create(fd);
      router.push('/dashboard/patients');
    } catch (err: any) {
      setError(err.response?.data?.error || 'Failed to create patient');
    }
  };

  return (
    <div className="space-y-6">
      <div>
        <h2 className="text-3xl font-bold text-gray-900">New Patient</h2>
        <p className="text-gray-600 mt-1">Add a new patient to your practice</p>
      </div>
      <Card className="shadow-lg border-gray-200">
        <CardHeader className="bg-gradient-to-r from-blue-50 to-indigo-50 border-b">
          <CardTitle className="text-xl">Patient Information</CardTitle>
        </CardHeader>
        <CardContent className="pt-6">
          <form onSubmit={handleSubmit} className="space-y-6 max-w-3xl">
            <Input label="Name" value={form.name} onChange={(e) => setForm({ ...form, name: e.target.value })} required />
            <Input label="DOB" type="date" value={form.dob} onChange={(e) => setForm({ ...form, dob: e.target.value })} />
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              <Input label="Email" type="email" value={form.email} onChange={(e) => setForm({ ...form, email: e.target.value })} />
              <Input label="Contact" value={form.contact} onChange={(e) => setForm({ ...form, contact: e.target.value })} />
            </div>
            <Input label="Disease" value={form.disease} onChange={(e) => setForm({ ...form, disease: e.target.value })} />
            <Input label="History" value={form.history} onChange={(e) => setForm({ ...form, history: e.target.value })} />
            <Input label="Prescription" value={form.prescription} onChange={(e) => setForm({ ...form, prescription: e.target.value })} />
            <div className="pt-2">
              <label className="block text-sm font-medium text-gray-900 mb-2">Medical Images</label>
              <div className="border-2 border-dashed border-gray-300 rounded-xl p-6 hover:border-blue-400 transition-colors">
                <input 
                  type="file" 
                  multiple 
                  onChange={(e) => setFiles(e.target.files)} 
                  className="w-full text-sm text-gray-600 file:mr-4 file:py-2 file:px-4 file:rounded-lg file:border-0 file:text-sm file:font-semibold file:bg-blue-50 file:text-blue-700 hover:file:bg-blue-100 file:cursor-pointer"
                />
              </div>
            </div>
            {error && <div className="bg-red-50 text-red-700 px-4 py-3 rounded-lg text-sm font-medium">{error}</div>}
            <div className="flex gap-3 pt-4">
              <Button type="submit" className="flex-1 py-3 bg-gradient-to-r from-blue-600 to-indigo-600 hover:from-blue-700 hover:to-indigo-700 shadow-lg font-semibold">
                <svg className="w-5 h-5 mr-2" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M5 13l4 4L19 7" />
                </svg>
                Create Patient
              </Button>
              <Button type="button" variant="outline" onClick={() => router.push('/dashboard/patients')} className="px-6">
                Cancel
              </Button>
            </div>
          </form>
        </CardContent>
      </Card>
    </div>
  );
}
